import React, { useState } from "react";
import clases from './Paginator.module.css';
import cn from 'classnames';

let Paginator = ({totalItemsCount, pagesSize, currentPage, onPageChanged, portionSize = 10}) => {

	let pagesCount = Math.ceil (totalItemsCount / pagesSize);
	let pages = [];
	for (let i = 1; i <= pagesCount; i++) {
		pages.push(i);
	};

	let portionCount = Math.ceil (pagesCount / portionSize);
	let [portionNumber, setPortionNumber] = useState(1);
	let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
	let rightPortionPageNumber = portionNumber * portionSize;

	return (
		<div className = {clases.paginator}> 
			{ portionNumber > 1 && 
			<button className = {clases.buttonL} onClick = {() => { setPortionNumber(portionNumber - 1)}}>Prev</button>}
			{pages
				.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
				.map(p => {
				return <span className={cn({
					[clases.selectedPage] : currentPage === p 
				}, clases.pageNumber)}
					key = {p}
					onClick={(e) => {
						onPageChanged(p);
					}}>{p}</span>
			})}
			{ portionCount > portionNumber &&
				<button className = {clases.buttonR} onClick = {() => {setPortionNumber(portionNumber + 1)}}>Next</button>}
		</div>
	)	
}			

export default Paginator;